import { useEnhancedDocument } from "@rp/react/common/rpjson"
import { RpJsonDocument } from "@rp/react/rpjson/types"
import { createReactElements } from "../../create-react-elements"

type Props = {
  rpjson: RpJsonDocument
  className?: string
}

export const RpJsonTitleDisplay = ({ rpjson, className = "" }: Props) => {
  const { title: parsedTitle } = useEnhancedDocument(rpjson)
  return (
    <div className={className}>
      {createReactElements(parsedTitle, {}, { performance: { markupSentences: true } })}
    </div>
  )
}
